import uParse from '@/components/u-parse/u-parse.vue'
import uniIcon from "@/components/uni-icon/uni-icon.vue"
import "@/components/quill/quill.core.css";
import "@/components/quill/quill.snow.css";
import "@/components/quill/quill.bubble.css";
export default {
	components: {
		uParse,
		uniIcon
	},
	data() {
		return {
			height: 0,
			name:"",
			icon:"",
			article:"",
			address:"",
			tel:"",
			cost:0,
			lat:0,
			lng:0
		}
	},
	onLoad(options) {
		this.cost = options.cost;
		this.checkLogin();
		this.height = uni.getSystemInfoSync().windowHeight;
		uni.showLoading({mask:true});
		this.getData(options.no);
	},
	methods: {
		preview(src, e) {
			// do something
		},
		navigate(href, e) {
			// do something
		},
		getData(No){
			uni.request({
				url: this.apiServer + 'merchant/merchantData',
				method: 'POST',
				data: {
					merchantNo:No
				},
				header: {
					'content-Type': 'application/x-www-form-urlencoded',
					'token': uni.getStorageSync('opid')
				},
				success: (res) => {
					if(res.data.status == 200){
						let data = res.data.data;
						if(!data.merchantIoc){
							this.icon = '../../static/index/u207.png'
						}else{
							this.icon = data.merchantIoc;
						};
						this.lat = parseFloat(data.lng);
						this.lng = parseFloat(data.lat);
						this.article = data.merchantIntroduce;
						this.tel = data.merchantMobile;
						this.address = data.merchantAddress;
						this.name = data.merchantName;
					}else if(res.data.status == 588){
						this.checkLogin();
						uni.showToast({
							title: '登录失效，请再试一次',
							icon: 'none',
							duration: 2000
						});
					}else{
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (res) => {
					uni.showToast({
						title: '网络异常',
						icon: 'none',
						duration: 2000
					});
					setTimeout(() => {
						uni.navigateBack({url: '../index/index'});
					}, 2000);
				},
				complete: (res) => {
					uni.hideLoading();
				}
			});
		},
		locationMap(){
			uni.openLocation({
				latitude: this.lat,   //纬度
				longitude: this.lng,   //经度
				name: this.name,
				address: this.address,
				success: function () {
					console.log('success');
				}
			});
		}
	}
}
